import React from 'react';
import { Bar } from 'react-chartjs-2';

export default function Info_Chart({SingleStateData , CountryData}){

    const data_barChart = SingleStateData ? (<Bar
    data={{
      labels: ["No of Cases", "Recovered", "Deaths"],
      datasets: [
        {
          label: "People",
          backgroundColor: [
            "rgba(44, 130, 201, 1)",
            "rgba(0, 255, 0, 0.5)",
            "rgba(255, 0, 0, 0.5)"
          ],
          data: [SingleStateData.noOfCases, SingleStateData.cured, SingleStateData.deaths]
        }
      ]
    }}
    option={{
      title: { display: true, text: `${SingleStateData.state}` }
    }}
  />) : (<Bar
    data={{
      labels: ["Infected", "Recovered", "Deaths"],
      datasets: [
        {
          label: "People",
          backgroundColor: [
            "rgba(0, 0, 255, 0.5)",
            "rgba(0, 255, 0, 0.5)",
            "rgba(255, 0, 0, 0.5)"
          ],
          data: [CountryData["Active cases"] , CountryData["Cured/Discharged/Migrated"] , CountryData["Death"]]
        }
      ]
    }}
    option={{
      title: { display: true, text: `India` }
    }}
  />)
  return (
    <div>{data_barChart}</div>
  )
}