import React , {useState , useEffect } from 'react';
import ReactMapGl from 'react-map-gl';
import Pins from "./components/Pins/Pins"
import Info_Card from "./components/Info/Card"
import {country_data}  from "./api";
import "./App.css"


export default function App(){

  const [ SingleStateData , setSingleStateData] = useState(null);
  const [ CountryData , setCountryData] = useState({});

  const [viewport , setViewport] = useState({
    latitude:20.5937,
    longitude:78.9629,
    width:window.innerWidth,
    height:window.innerHeight,
    zoom:4
  });
  useEffect(() => {
    const handleResize = () => {
      setViewport({
        ...viewport,
        width: window.innerWidth,
        height: window.innerHeight,
      });
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });

  useEffect(() => {
    const dataFunc = async () => {
      const fetchedData = await country_data();
      setCountryData(fetchedData.data);
    };
    dataFunc();
  }, [])

  

  return (
    <ReactMapGl
      {...viewport}
      mapboxApiAccessToken={process.env.REACT_APP_MAPBOX_TOKEN}
      mapStyle="mapbox://styles/mayankb02/ckaxk7z3p01gh1io1qs92uj4j"
      onViewportChange={viewport => { setViewport(viewport); }} >
      <Pins setSingleStateData = {setSingleStateData} />
      <Info_Card setSingleStateData = {setSingleStateData} SingleStateData={SingleStateData} CountryData={CountryData} />
    </ReactMapGl>
  )
}