import axios from 'axios';

export const state_data = async () => {
    try {
        const {data}  = await axios.get('https://covid-india-cases.herokuapp.com/states');
        return {data} ;
    } catch (error){
        console.log(error);
    }
}

export const country_data = async () => {
    try {
        const fetchedData  = await axios.get('http://covid19-india-adhikansh.herokuapp.com/summary');
        return fetchedData ;
    } catch (error){
        console.log(error);
    }
}