import React from 'react';
import Info_Chart from "./Chart";
import img from "./IndiaEmoji.png";
import styles from "./Info.module.css";
import { Card , CardHeader , CardContent , Typography , Grid }  from '@material-ui/core';

export default function Info_Card({setSingleStateData , SingleStateData , CountryData}){
    return (
        <Grid container className={styles.grid_container}>
            <Card className={styles.card_container}>
                <CardHeader title={ SingleStateData ? SingleStateData.state  : "India"}
                 subheader={ SingleStateData ? "India - Covid-19 Tracker" : "Covid-19 Tracker"}
                 action = {<img style={{width:"70px" , height:"70px"}} onClick={()=>setSingleStateData(null)} src={img}/>} />
                <CardContent>
                    <Typography variant="subtitle1">
                        Number of confirmed cases : {SingleStateData ? SingleStateData.noOfCases : CountryData["Total Cases"]}
                    </Typography>
                    <Typography  variant="subtitle1">
                        Number of deaths due to Covid-19 : {SingleStateData ? SingleStateData.deaths : CountryData["Death"] }
                    </Typography>
                    <Typography  variant="subtitle1">
                        Number of recovered cases : {SingleStateData ? SingleStateData.cured : CountryData["Cured/Discharged/Migrated"]}
                    </Typography>
                    <Info_Chart SingleStateData={SingleStateData} CountryData={CountryData} />
                </CardContent>
            </Card>
        </Grid>
    )
}